import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import { FinishedProductComponent } from './finished-product.component';

export const ROUTES: Routes = [
  {
    path: '',
    component: FinishedProductComponent
  }
];
@NgModule({
  imports: [
    RouterModule.forChild(ROUTES)
  ],
  exports: [
    RouterModule
  ]
})
export class FinishedProductRouteModule {
}