import { Component, OnInit, ElementRef, ViewChild, ViewContainerRef, Input, 
  AfterViewInit, AfterContentInit } from '@angular/core';
import { OperateEnum } from '../../../shared/bd-tool-bar/bd-tool-bar.component';
import { Router } from '@angular/router';

// 第三方
import { NzModalService,NzTreeNode, NzSliderStepComponent, NzTreeNodeOptions } from 'ng-zorro-antd';

// 下面自定义对象的导入
import { FinishedProductService } from '../../../shared/service/finished-product.service';
import * as model from '../../../shared/model';
import { SpellZhService } from 'src/app/shared/service/spell-zh.service';

@Component({
  selector: 'app-content',
  templateUrl:'./content.component.html',
  styleUrls :['./content.component.css'],
  providers:[SpellZhService]
})
export class ContentComponent implements OnInit,AfterViewInit,AfterContentInit {

    //#region UI双向绑定的属性
    private selectedNode:NzTreeNode=null;// 树形组件当前选中的节点
    private selectedNodeParentTitle:string='';// 树形组件当前选中的节点的父节点
    private node4Editor:model.CTreeNode=new model.CTreeNode();// 绑定在编辑页面的节点对象
    private treeData:NzTreeNode[]=[];
    private nodeDrawerVisible=false;// 节点抽屉开关
    private nodeSaveStatus:OperateEnum;
    private displayCols:model.TableStyle[];// 表格中显示出来的列构成的数组
    private selectedCols:string[]=[];//nz-select 组件中被选中（即primeng网格中显示的列对应表tableStyle的字段名称）
    private tableData:model.FinishedProduct[];// 表格数据
    private selectedRow:model.FinishedProduct;// 表格中当前选中的行
    private tableStyle:model.TableStyle[];
    private tableDataStatus:string='1';

    private rowSaveStatus:OperateEnum;//标记当前行数据是新增还是修改
    private row4Editor:model.FinishedProduct=new model.FinishedProduct();
    private rowDrawerVisible=false;
    private rowVRName:model.ValidateResult = new model.ValidateResult();
    private rowVRSid:model.ValidateResult=new model.ValidateResult();
    private rowVRParentId:model.ValidateResult=new model.ValidateResult();
    


    @ViewChild('nodeName')
    private ctlNodeName:ElementRef;

    @ViewChild('goodsName')
    private ctlGoodsName:ElementRef;
    //#endregion


  constructor(
    private service:FinishedProductService,
    private modalService:NzModalService,
    private spellService:SpellZhService,
    private router:Router
  ){}

  ngAfterContentInit(){}
  ngAfterViewInit(){}
  ngOnInit(){
    this.buildTreeByAllValid();// 构建树形
    this.initTableStyle();// 初始化表格组件要显示的列
    this.getTableDataByStatus();//显示表格数据
  }



  // 从后端获取所有有效数据组成的数组
  buildTreeByAllValid(){
    console.log('进入buildTreeByAllValid');
    this.service.buildTreeByAllValid().subscribe(
      (val)=>{
        this.treeData = val;
        console.log(this.treeData);
        // this.setTreeRoot(this.treeData);
        // console.log(this.treeData);
        
      },
      error=>{ 
        console.log('获取树形数据出错！');
      }
    );
  }

  // 为树形数据填充一个根
  setTreeRoot(tree:NzTreeNode[]){
    var root:NzTreeNode;
    root.key='0';
    root.title='全部';
    tree.push(root);

  }

  //#region 树形组件CRUD
  insertNodeSibling(){// 新增同级节点
    //1. 构造空的实例
    this.nodeSaveStatus=OperateEnum.insert;

    this.node4Editor = new model.CTreeNode();
    var fatherNode:NzTreeNode = this.selectedNode.getParentNode();
    console.log('获取到的父节点是：');
    console.log(fatherNode);
    
    if(fatherNode===null){
      this.node4Editor.parentid = 0;  
      console.log('新节点的上级节点是：0');
      
    }else{
      this.node4Editor.parentid = parseInt(fatherNode.key);  
      console.log('新节点的上级节点是：' + fatherNode.key);
    }

    //2. 设置所属上级的title
    this.selectedNodeParentTitle = this.selectedNode.getParentNode().title;

    //3. 显示编辑页面
    this.openNodeDrawer();
  }

  insertNodeChild(){// 新增下级节点
    //1. 构造空的实例
    this.nodeSaveStatus=OperateEnum.insert;

    this.node4Editor = new model.CTreeNode();
    this.node4Editor.parentid = parseInt(this.selectedNode.key);

    //2. 设置所属上级的title
    this.selectedNodeParentTitle = this.selectedNode.title;

    //3. 显示编辑页面
    this.openNodeDrawer();
  }

  //新增树形节点
  insertNode(){
    //同名不可添加
    this.service.isExistByNodeTitle(this.node4Editor.title).subscribe(
      (val:boolean)=>{
        if(true===val){
          this.modalService.error({
            nzTitle:'该名称已经存在！',
            nzContent:'',
            nzOnOk:()=>{return;}
          });
        }else{// 不存在同名的才进行新增
          this.service.insertNode(this.node4Editor);
          this.closeNodeDrawer();
          this.buildTreeByAllValid();
        }
      },
      error=>{
        console.log('鉴定是否存在重名时出错！');
      }
    );
  }


  deleteNode(){
    if(this.selectedNode===null){
      this.modalService.warning({
        nzTitle:'选中一个节点后再做本操作！',
        nzContent:'',
        nzOnOk:()=>{return;}
      });
    }

    this.modalService.confirm({
      nzTitle:'您确定要删除选中的节点么？',
      nzContent:'',
      nzOnOk:()=>{
        this.service.deleteNode(this.selectedNode.key).subscribe(
          val=>{
            this.buildTreeByAllValid();
          },
          error=>{
            console.log('删除节点时出错！');
          }
        );
      }
    });

  }

  // 保存对于节点的编辑
  saveNode(){
    if(this.nodeSaveStatus === OperateEnum.insert){
      this.insertNode();
    }

    if(this.nodeSaveStatus === OperateEnum.update){
      this.doUpdateNode();
    }

  }

  doUpdateNode(){
    this.service.updateNode(this.node4Editor).subscribe(
      val=>{
        this.closeNodeDrawer();
        this.buildTreeByAllValid();
      },
      error=>{console.log('修改节点时候出粗！');}
    );
  }

  updateNode(){    
    if(this.selectedNode===null){
      this.modalService.warning({
        nzTitle:'选中节点后再执行本操作！',
        nzContent:'',
        nzOnOk:()=>{return;}
      });
    }else{
      this.nodeSaveStatus = OperateEnum.update;
      this.node4Editor=this.convertNzNode2CTreeNode(this.selectedNode);
      this.openNodeDrawer();
    }
  }

  // 将当前选中的节点（NzTreeNode）转换为自定义的类型 - CTreeNode
  convertNzNode2CTreeNode(nzNode:NzTreeNode):model.CTreeNode{
    var node = new model.CTreeNode();
    node.id=parseInt(nzNode.key);
    node.key=nzNode.key;
    node.title=nzNode.title;
    node.parentid = this.getNzNodeParentid(nzNode);
    node.oprimary=parseInt(nzNode.key);

    return node;
  }

  getNzNodeParentid(node:NzTreeNode){
    var parent:NzTreeNode = node.getParentNode();
    var rtn:number;
    if(parent===null){
      this.selectedNodeParentTitle='根节点';
      return 0;
    }else{
      this.selectedNodeParentTitle=parent.title;
      return parseInt(parent.key);
    }
  }

  //打开编辑树形节点的抽屉
  openNodeDrawer(){
    this.nodeDrawerVisible=true;
    setTimeout(() => {
      this.ctlNodeName.nativeElement.focus();
    }, 300);
  }

  // 关闭节点编辑的抽屉组件
  closeNodeDrawer(){
    this.nodeDrawerVisible=false;
  }


  //#endregion



  //#region 网格组件相关
  // 获取网格样式
  initTableStyle(){
    this.service.getTableStyle().subscribe(
      (val:model.TableStyle[])=>{
        this.tableStyle=val.concat();
        this.displayCols=val.concat();
        this.decorateDisplayCols(this.displayCols);// 增量装饰
        console.log('displayCols:');
        console.log(this.displayCols);
        
      },
      error=>{
        console.log('在获取表格样式时出错！');
      }
    );
  }

  decorateDisplayCols(arr:model.TableStyle[]){
    arr.forEach(element => {
      element.field = element.fieldname;
      element.header = element.caption;
      element.width =  element.colwidth.toString() + 'px';

      // 同时初始化nz-select的被选中的列
      this.selectedCols.push(element.fieldname);
    });
  }


  // nz-select 值变化时触发
  onColSelectorChange(){
    while(this.displayCols.length>0){
      var ele=this.displayCols.pop();
      console.log('清空掉的元素是：');
      console.log(ele);
    }

    console.log('数组清空后是：');
    console.log(this.displayCols);
    console.log('数组tableStyle打印后是：');
    console.log(this.tableStyle);
    
    
    
    var ele1:model.TableStyle;
    this.selectedCols.forEach(element => {
      console.log('进入selectedCols循环');
      console.log(this.tableStyle);
      
      this.tableStyle.forEach(ele => {
        console.log('进入tableStyle循环');
        this.displayCols.find
        if(ele.fieldname===element){
          var e:model.TableStyle = new model.TableStyle();
          e.alignment=ele.alignment;
          e.backcolor=ele.backcolor;
          e.caption=ele.caption;
          e.colwidth=ele.colwidth;
          e.createdate=ele.createdate;
          e.dbname=ele.dbname;
          e.field=ele.field;
          e.fieldname=ele.fieldname;
          e.forecolor=ele.forecolor;
          e.header=ele.header;
          e.hide=ele.hide;
          e.iid=ele.iid;
          e.isQuantity=ele.isQuantity;
          e.locked=ele.locked;
          e.notnull=ele.notnull;
          e.sole=ele.sole;
          e.tablename=ele.tablename;

          console.log('向displayCols中追加一个元素：');
          console.log(e);
          this.displayCols.push(e);
        }
      });
    });

    console.log(this.displayCols);
    
  }


  // 根据当前的分类显示对应的数据
  onStatusChange(){
    console.log('进入onStatusChange');
    
    this.getTableDataByStatus();
  }

  getTableDataByStatus(){
    if(this.tableDataStatus==='1'){// 仅有效
      console.log('即将显示：仅有效');
      this.getTableDataAllValid();
    }

    if(this.tableDataStatus==='0'){// 仅无效
      console.log('即将显示：仅无效');
      this.getTableDataAllInvalid();
    }

    if(this.tableDataStatus==='9'){// 全部
      console.log('即将显示：全部');
      this.getTableDataAll();
    }
  }

  // 获取表格数据 - 仅有效
  getTableDataAllValid(){
    this.service.getTableDataAllValid().subscribe(
      val=>{
        this.tableData = val;
      },
      error=>{console.log('在获取表格数据 - 仅有效时出错！');
      }
    );
  }

  // 获取表格数据 - 仅无效
  getTableDataAllInvalid(){
    this.service.getTableDataAllInvalid().subscribe(
      val=>{
        this.tableData = val;
      },
      error=>{console.log('在获取表格数据 - 仅无效时出错！');
      }
    );
  }

  // 获取表格数据 - 所有
  getTableDataAll(){
    this.service.getTableDataAll().subscribe(
      val=>{
        this.tableData = val;
      },
      error=>{console.log('在获取表格数据 - 全部时出错！');
      }
    );
  }


  //#endregion

  //#region 树形组件的事件
  onNodeClick(e){
    this.selectedNode = e.node;
    console.log(this.selectedNode);
    

    // 点击节点后显示该节点下的所有子节点的数据
    this.getAllChildren();
  }

  onNodeNameChange(){
  }
  //#endregion


  //#region 表格CRUD
  insertRow(){
    this.rowSaveStatus=OperateEnum.insert;
    this.row4Editor=new model.FinishedProduct();
    this.openRowDrawer();
  }

  updateRow(){
    this.rowSaveStatus=OperateEnum.update;
    this.service.cloneFinishedProduct(this.selectedRow,this.row4Editor);
    this.row4Editor.oprimary = this.row4Editor.sid;
    this.openRowDrawer();
  }

  doInsertRow(){
    this.service.insertRow(this.row4Editor).subscribe(
      val=>{
        this.getTableDataByStatus();
        this.closeRowDrawer();
      },
      error=>{console.log('向数据库插入一行数据时出错！');}
    );
  }

  doUpdateRow(){
    console.log('修改的最终结果是：');
    console.log(this.row4Editor);
    
    
    this.service.updateRow(this.row4Editor).subscribe(
      val=>{
        this.getTableDataByStatus();
        this.closeRowDrawer();
      },error=>{console.log('修改行数据时出错！');}
    );
  }

  deleteRow(){
    this.modalService.confirm({
      nzTitle:'您确定要删除选中行？',
      nzContent:'',
      nzOnOk:()=>{
        this.service.deleteRow(this.selectedRow.sid).subscribe(
          val=>{
            this.getTableDataByStatus();
          },error=>{console.log('删除数据时出错！');}
        );
      }
    });
  }

  openRowDrawer(){
    this.rowDrawerVisible=true;
    setTimeout(() => {
      this.ctlGoodsName.nativeElement.focus();
    }, 300);
  }

  validate4SaveRow():boolean{
    console.log('进入validate4SaveRow');
    
    var rtn:boolean=true;

    if(this.row4Editor.name===undefined||this.row4Editor.name===''||this.row4Editor.name.length<=0){
      this.rowVRName.setSuccess(false);
      this.rowVRName.explain='品名不可为空！';
      rtn=false;
    }

    if(this.row4Editor.sid===undefined||this.row4Editor.sid===''||this.row4Editor.sid.length<=0){
      this.rowVRSid.setSuccess(false);
      this.rowVRSid.explain='编号不可为空！';
      rtn=false;
    }

    this.service.getTableDataOne(this.row4Editor.sid).subscribe(
      val=>{
        if(null!==val){
          this.rowVRSid.setSuccess(false);
          this.rowVRSid.explain='已存在相同的编号！';
          rtn=false;
        }
      },
      error=>{console.log('查询获取指定编号的对象时出错！');}
    );

    console.log('validate4SaveRow.parentid=' + this.row4Editor.parentid);
    
    if(this.row4Editor.parentid===undefined||this.row4Editor.parentid<=0){
      this.rowVRParentId.setSuccess(false);
      this.rowVRParentId.explain='必须选择所属上级！';
      rtn=false;
    }

    return rtn;
  }
  //#endregion

  //#region 表格工具栏事件
  onMenuItemClick(e:OperateEnum){
    if(e===OperateEnum.insert){
      this.insertRow();
    }

    if(e===OperateEnum.update){
      this.updateRow();
    }

    if(e===OperateEnum.delete){
      this.deleteRow();
    }

    if(e===OperateEnum.print){// 打印预览
      this.openPrintPreview();
    }

  }

  //#endregion

  //#region 表格事件
  onRowSelect(e){// 将当前选中行数据克隆
    //this.row4Editor = this.selectedRow.clone();
    this.row4Editor = new model.FinishedProduct();
    //this.selectedRow.clone(this.row4Editor);
    this.service.cloneFinishedProduct(this.selectedRow,this.row4Editor);
  }

  afterColResize(){}

  afterSort(){}

  closeRowDrawer(){
    this.rowDrawerVisible=false;
  }

  onNameChange(){// 根据品名自动获取拼音
    if(this.rowSaveStatus===OperateEnum.insert){
      this.row4Editor.sid = this.spellService.getFirstSpell(this.row4Editor.name);
    }
    this.rowVRName=new model.ValidateResult();
  }

  onSidChange(){
    this.rowVRSid=new model.ValidateResult();
  }


  saveRowEditor(){// 保存表格抽屉的数据
    if(this.validate4SaveRow()===false){
      return;
    }

    if(this.rowSaveStatus===OperateEnum.insert){
      this.doInsertRow();
    }

    if(this.rowSaveStatus===OperateEnum.update){
      this.doUpdateRow();
    }

  }
  //#endregion

  //#region 列选择器的事件
  onBlurHideCol(e){

  }

  onPanelHideHideCol(e){

  }

  onChangeHideCol(e){

  }
  //#endregion

  onTreeSelectChange(e){// 表格数据抽屉树选择组件的变更事件
    this.row4Editor.parentid=e;
    this.rowVRParentId=new model.ValidateResult();
  }


  getSpeEle(){
      var newArr:model.FinishedProduct[]=[];
      newArr=this.tableData.concat();
      console.log('链接后的新的数组是：');
      console.log(newArr);
      
      newArr.pop();
      console.log('pop一次后新数组是：');
      console.log(newArr);
      console.log('原始数组是：');
      console.log(this.tableData);
  }

  /**
   * 获取选择节点下的所有子节点的产品数据
   */
  getAllChildren(){
    var parentId:number=parseInt(this.selectedNode.origin.key);
    console.log('选中节点的key=' + parentId);
    
    this.service.getTableAllChildren(parentId).subscribe(
      val=>{
        this.tableData=val;
      },error=>{console.log('获取指定节点下的所有子节点的数据时出错！');}
    );
  }

  // 显示打印预览
  openPrintPreview(){
    this.router.navigate(['frfp']);
  }
}
