import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

// ng-zorro-antd
import { NzTreeNode } from 'ng-zorro-antd';

// customer
import * as url from '../url';
// import { CTreeNode,TableStyle,FinishedProductModel } from './model';
import * as model from '../../shared/model';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class FinishedProductService {

  constructor(
    private http:HttpClient,
    private fpcUrl:url.FinishedProductCategory,
    private fpUrl:url.FinishedProduct,
    private tableStyleUrl:url.TableStyle
    ) { }

  // 返回树形组件的数据源
  buildTreeByAllValid():Observable<NzTreeNode[]>{
    return this.http.get<NzTreeNode[]>(this.fpcUrl.treeAllValid);
  }  

  // 新增树形节点
  insertNode(node:model.CTreeNode){
    this.http.post(this.fpcUrl.insert,node).subscribe(
      (val)=>{
        console.log('通过url:' + this.fpcUrl.insert + '新增节点成功：');
        console.log(val);
        
        // 刷新树形组件
        this.buildTreeByAllValid();
      },
      error=>{
        console.log('新增节点失败！');
      }
    );
  }

  // 通过节点的名字查询是否有同名的节点
  isExistByNodeTitle(nodeTitle:string):Observable<boolean>{
    return this.http.get<boolean>(this.fpcUrl.isExistByName + '/' + nodeTitle);
  }

  deleteNode(key:string):Observable<Number>{
    return this.http.get<Number>(this.fpcUrl.delete + '/' + key);
  }

  updateNode(node:model.CTreeNode):Observable<Number>{
    return this.http.post<Number>(this.fpcUrl.update,node);
  }

  getTableStyle():Observable<model.TableStyle[]>{
    return this.http.get<model.TableStyle[]>(this.tableStyleUrl.getSingleTable + '/finishedproduct');
  }

  // 获取表格数据 - 仅有效
  getTableDataAllValid():Observable<model.FinishedProduct[]>{
    return this.http.get<model.FinishedProduct[]>(this.fpUrl.getAllValid);
  }

  // 获取表格数据 - 仅无效
  getTableDataAllInvalid():Observable<model.FinishedProduct[]>{
    return this.http.get<model.FinishedProduct[]>(this.fpUrl.getAllInvalid);
  }  

  // 获取表格数据 - 所有
  getTableDataAll():Observable<model.FinishedProduct[]>{
    return this.http.get<model.FinishedProduct[]>(this.fpUrl.getAll);
  }  

  getTableDataOne(sid:string):Observable<model.FinishedProduct>{
    return this.http.get<model.FinishedProduct>(this.fpUrl.getByPrimaryKey + '/' + sid);
  }

  getTableAllChildren(parentId:number):Observable<model.FinishedProduct[]>{
    var url = this.fpUrl.getAllChildren + '/'+parentId;
    console.log(url);
    return this.http.get<model.FinishedProduct[]>(url);
  }


  // 下面是表格CRUD
  insertRow(data:model.FinishedProduct):Observable<number>{
    return this.http.post<number>(this.fpUrl.insert,data);
  }

  deleteRow(sid:string):Observable<number>{
    return this.http.get<number>(this.fpUrl.delete + '/' + sid);
  }

  updateRow(data:model.FinishedProduct):Observable<number>{
    return this.http.post<number>(this.fpUrl.update,data);
  }

  cloneFinishedProduct(o:model.FinishedProduct,t:model.FinishedProduct){
    t.sid=o.sid;
    t.name=o.name;
    t.specification=o.specification;
    t.parentid=o.parentid;
    t.serialno=o.serialno;
    t.status=o.status;
    t.createdate=o.createdate;
    t.oprimary=o.oprimary;
    t.parentTitle = o.parentTitle;
  }


}
