import { Component, OnInit, OnChanges } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NzFormatEmitEvent,NzTreeNode,NzTreeNodeOptions } from 'ng-zorro-antd';




export class TreeNodeModel{// 树形节点模型
  constructor(
    public title:string,
    public key:string,
    public expanded:boolean,
    public children:TreeNodeModel[]
  ){}
}

export class TableRowModel{// 对应的DB中的表的模型
  constructor(
    public id:number,
    public name:string,
    public parentid:number,
    public oPrimary:number
  ){}
}

export class NodeOptions implements NzTreeNodeOptions{
  title: string;
  key: string;
  icon?: string;
  isLeaf?: boolean;
  checked?: boolean;
  selected?: boolean;
  selectable?: boolean;
  disabled?: boolean;
  disableCheckbox?: boolean;
  expanded?: boolean;
  children?: NzTreeNodeOptions[];
  [key: string]: any;
}



@Component({
  selector: 'app-finished-product',
  templateUrl: './finished-product.component.html',
  styleUrls: ['./finished-product.component.css']
})
export class FinishedProductComponent implements OnInit,OnChanges {

 
  private urlSelectAll:string = 'http://localhost:8085/femis/finishedProductCategory/selectAll';
  private nodesAll:NzTreeNodeOptions[] = [];
  private testNodes:any[] = [];
  private rows:TableRowModel[];




  constructor(
    private http:HttpClient
  ){
    
  }

  nzEvent(event: NzFormatEmitEvent): void {
    console.log(event);
  }

  ngOnInit(): void {
    // this.initTree();// 初始化树形组件，填充数据
    // console.log(this.nodesAll);
    // console.log(typeof(this.nodesAll));
    
    

  //   this.testNodes=[
  //   {title:'珊瑚绒',key:'1',expanded:true,children:[
  //     {title:'珊瑚绒01',key:'11',expanded:true}
  //   ]},
  //   {title:'法兰绒',key:'2',expanded:true}
  // ];

  //   console.log(this.testNodes);
  //   console.log(typeof(this.testNodes));
    
  }

  ngOnChanges():void{
    
  }

  initTree(){// 初始化树形组件，填充数据
    this.http.get(this.urlSelectAll).subscribe(
      (data:any[])=>{
        this.rows = data;//2018年9月26日 23:13:35测试通过
        // console.log('根递归前打印的rows:');
        // console.log(this.rows);
        //从根节点递归
        this.rootRecursion(0);

        console.log('成品组件.树形组件的数据源：');
        console.log(this.nodesAll);
        
      },
      error=>{
        console.log('获取所有数据时出错！');
      }
    );
  }


  rootRecursion(fatherId:number){
    var partRows = this.rows.filter((val,i) => val.parentid===fatherId);

    partRows.forEach(element=>{  
      var node:NodeOptions = this.getNewNode(element.id);
      node.children = this.getChildrenNodes(element.id);
      this.nodesAll.push(node);

      node.children.forEach((val,i)=>this.rootRecursion(Number(val.key)));
    });

  }


  getNewNode(id:number):NzTreeNodeOptions{
    var row:TableRowModel = this.rows.filter((val,i)=>val.id===id)[0];//1. 获取指定行
    var opts:NodeOptions = new NodeOptions();//2. 构建节点参数
    opts.title = row.name;
    opts.key = row.id.toString();
    opts.expanded = true;
    opts.children = null;
    return opts;
  }


  getChildrenRows(id:number):TableRowModel[]{//获取某个节点下的所有子节点
    return this.rows.filter((val,i)=>val.parentid === id);
  }

  getChildrenNodes(id:number):NzTreeNodeOptions[]{
    var childrenRows = this.getChildrenRows(id);
    var childrenNodes:NzTreeNodeOptions[] = [];
    childrenRows.forEach((element) => {childrenNodes.push(this.getNewNode(element.id))});
    return childrenNodes;
  }

}
